'use client';

import { useState } from 'react';
import { AppLayout } from '@/components/layout/AppLayout';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { useAuth } from '@/components/auth/SessionManager';
import { 
  Play, 
  Clock, 
  Target, 
  Zap, 
  Brain,
  Code,
  Users,
  Briefcase,
  ArrowRight,
  Star
} from 'lucide-react';

interface QuickPracticeOption {
  id: string;
  title: string;
  description: string;
  duration: number;
  questions: number;
  icon: any;
  difficulty: 'Easy' | 'Medium' | 'Hard';
  category: string;
  popular?: boolean;
}

export default function PracticePage() {
  const { user } = useAuth();
  const [loading, setLoading] = useState<string | null>(null);

  const quickPracticeOptions: QuickPracticeOption[] = [
    {
      id: 'behavioral-basics',
      title: 'Behavioral Basics',
      description: 'Common behavioral questions to get you started',
      duration: 15,
      questions: 5,
      icon: Users,
      difficulty: 'Easy',
      category: 'Behavioral',
      popular: true
    },
    {
      id: 'technical-warmup',
      title: 'Technical Warm-up',
      description: 'Light technical questions to practice problem-solving',
      duration: 20,
      questions: 4,
      icon: Code,
      difficulty: 'Medium',
      category: 'Technical'
    },
    {
      id: 'leadership-scenarios',
      title: 'Leadership Scenarios',
      description: 'Practice leadership and management questions',
      duration: 25,
      questions: 6,
      icon: Target,
      difficulty: 'Medium',
      category: 'Leadership'
    },
    {
      id: 'quick-fire',
      title: 'Quick Fire Round',
      description: 'Rapid-fire questions to improve response speed',
      duration: 10,
      questions: 8,
      icon: Zap,
      difficulty: 'Easy',
      category: 'General'
    },
    {
      id: 'system-design',
      title: 'System Design',
      description: 'Architecture and system design challenges',
      duration: 45,
      questions: 3,
      icon: Brain,
      difficulty: 'Hard',
      category: 'Technical'
    },
    {
      id: 'executive-level',
      title: 'Executive Level',
      description: 'Senior leadership and strategic thinking questions',
      duration: 30,
      questions: 5,
      icon: Briefcase,
      difficulty: 'Hard',
      category: 'Executive'
    }
  ];

  const handleStartPractice = async (optionId: string) => {
    setLoading(optionId);
    
    try {
      // Simulate starting practice session
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // In a real app, this would navigate to the practice session
      // For now, redirect to interview page with pre-configured settings
      const option = quickPracticeOptions.find(o => o.id === optionId);
      if (option) {
        const params = new URLSearchParams({
          mode: 'practice',
          type: option.category.toLowerCase(),
          duration: option.duration.toString(),
          questions: option.questions.toString(),
          difficulty: option.difficulty.toLowerCase()
        });
        
        window.location.href = `/interview?${params.toString()}`;
      }
    } catch (error) {
      console.error('Failed to start practice:', error);
    } finally {
      setLoading(null);
    }
  };

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'Easy': return 'text-green-600 bg-green-100';
      case 'Medium': return 'text-yellow-600 bg-yellow-100';
      case 'Hard': return 'text-red-600 bg-red-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'Behavioral': return 'text-blue-600 bg-blue-100';
      case 'Technical': return 'text-purple-600 bg-purple-100';
      case 'Leadership': return 'text-indigo-600 bg-indigo-100';
      case 'General': return 'text-gray-600 bg-gray-100';
      case 'Executive': return 'text-orange-600 bg-orange-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  return (
    <AppLayout>
      <div className="max-w-6xl mx-auto p-6">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Quick Practice</h1>
          <p className="text-gray-600 mt-2">
            Jump into focused practice sessions designed to improve specific skills
          </p>
        </div>

        {/* Usage Info */}
        {user && (
          <Card className="mb-8">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">Your Practice Usage</h3>
                  <p className="text-gray-600">
                    {user.subscription.tier === 'free' 
                      ? `${user.subscription.usage.sessionsUsed} of ${user.subscription.usage.sessionsLimit} sessions used this month`
                      : `${user.subscription.usage.sessionsUsed} sessions completed this month`
                    }
                  </p>
                </div>
                
                <div className="text-right">
                  <div className="text-2xl font-bold text-blue-600">
                    {user.subscription.tier === 'free' 
                      ? `${(user.subscription.usage.sessionsLimit || 0) - user.subscription.usage.sessionsUsed}`
                      : '∞'
                    }
                  </div>
                  <div className="text-sm text-gray-500">
                    {user.subscription.tier === 'free' ? 'remaining' : 'unlimited'}
                  </div>
                </div>
              </div>
              
              {user.subscription.tier === 'free' && user.subscription.usage.sessionsUsed >= (user.subscription.usage.sessionsLimit || 0) && (
                <div className="mt-4 p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                  <p className="text-yellow-800 text-sm">
                    You've reached your monthly limit. 
                    <Button variant="link" className="p-0 h-auto ml-1 text-yellow-800 underline">
                      Upgrade to Pro
                    </Button> 
                    for unlimited practice sessions.
                  </p>
                </div>
              )}
            </CardContent>
          </Card>
        )}

        {/* Quick Practice Options */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {quickPracticeOptions.map((option) => {
            const Icon = option.icon;
            const canStart = !user || 
              user.subscription.tier !== 'free' || 
              user.subscription.usage.sessionsUsed < (user.subscription.usage.sessionsLimit || 0);
            
            return (
              <Card key={option.id} className={`relative ${option.popular ? 'ring-2 ring-blue-500' : ''}`}>
                {option.popular && (
                  <div className="absolute -top-2 -right-2">
                    <div className="bg-blue-500 text-white px-2 py-1 rounded-full text-xs font-medium flex items-center">
                      <Star className="w-3 h-3 mr-1" />
                      Popular
                    </div>
                  </div>
                )}
                
                <CardHeader>
                  <div className="flex items-start justify-between">
                    <div className="flex items-center">
                      <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center mr-3">
                        <Icon className="w-5 h-5 text-blue-600" />
                      </div>
                      <div>
                        <CardTitle className="text-lg">{option.title}</CardTitle>
                      </div>
                    </div>
                  </div>
                  <CardDescription className="mt-2">
                    {option.description}
                  </CardDescription>
                </CardHeader>
                
                <CardContent>
                  <div className="space-y-4">
                    {/* Stats */}
                    <div className="flex items-center justify-between text-sm text-gray-600">
                      <div className="flex items-center">
                        <Clock className="w-4 h-4 mr-1" />
                        {option.duration} min
                      </div>
                      <div className="flex items-center">
                        <Target className="w-4 h-4 mr-1" />
                        {option.questions} questions
                      </div>
                    </div>
                    
                    {/* Tags */}
                    <div className="flex items-center justify-between">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getCategoryColor(option.category)}`}>
                        {option.category}
                      </span>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getDifficultyColor(option.difficulty)}`}>
                        {option.difficulty}
                      </span>
                    </div>
                    
                    {/* Action Button */}
                    <Button
                      className="w-full"
                      onClick={() => handleStartPractice(option.id)}
                      disabled={!canStart || loading === option.id}
                    >
                      {loading === option.id ? (
                        'Starting...'
                      ) : (
                        <>
                          <Play className="w-4 h-4 mr-2" />
                          Start Practice
                        </>
                      )}
                    </Button>
                    
                    {!canStart && (
                      <p className="text-xs text-gray-500 text-center">
                        Upgrade to continue practicing
                      </p>
                    )}
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>

        {/* Custom Practice */}
        <Card className="mt-8">
          <CardHeader>
            <CardTitle className="flex items-center">
              <Target className="w-5 h-5 mr-2" />
              Need Something Specific?
            </CardTitle>
            <CardDescription>
              Create a custom interview session tailored to your needs
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between">
              <div>
                <h4 className="font-medium text-gray-900">Custom Interview Setup</h4>
                <p className="text-sm text-gray-600 mt-1">
                  Choose your own role, company, question types, and duration
                </p>
              </div>
              <Button 
                variant="outline"
                onClick={() => window.location.href = '/interview'}
              >
                Custom Setup
                <ArrowRight className="w-4 h-4 ml-2" />
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Tips */}
        <Card className="mt-6">
          <CardHeader>
            <CardTitle>Practice Tips</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="text-center">
                <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                  <Target className="w-6 h-6 text-green-600" />
                </div>
                <h4 className="font-semibold mb-2">Start Easy</h4>
                <p className="text-sm text-gray-600">
                  Begin with easier sessions to build confidence before tackling harder challenges
                </p>
              </div>
              
              <div className="text-center">
                <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                  <Clock className="w-6 h-6 text-blue-600" />
                </div>
                <h4 className="font-semibold mb-2">Practice Regularly</h4>
                <p className="text-sm text-gray-600">
                  Consistent short sessions are more effective than occasional long ones
                </p>
              </div>
              
              <div className="text-center">
                <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mx-auto mb-3">
                  <Brain className="w-6 h-6 text-purple-600" />
                </div>
                <h4 className="font-semibold mb-2">Review Reports</h4>
                <p className="text-sm text-gray-600">
                  Always review your performance reports to identify areas for improvement
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </AppLayout>
  );
}